/* Global Styles and Font */
:root {
  --primary-color: #3b82f6; /* Tailwind Blue-500 */
  --secondary-color: #6b7280; /* Tailwind Gray-500 */
  --success-color: #22c55e; /* Tailwind Green-500 */
  --warning-color: #facc15; /* Tailwind Yellow-400 */
  --danger-color: #ef4444; /* Tailwind Red-500 */
  --text-color-dark: #1e293b; /* Tailwind Gray-800 */
  --text-color-light: #6b7280; /* Tailwind Gray-500 */
  --bg-gray-50: #f9fafb; /* Tailwind Gray-50 */
  --bg-white: #ffffff;
  --border-color: #e5e7eb; /* Tailwind Gray-200 */
}

body {
  font-family: "Poppins", sans-serif;
  background-color: var(--bg-gray-50);
  color: var(--text-color-dark);
}
/* Quiz Page Container */
.quizPageContainer {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  padding: 1.5rem;
  padding-bottom: 3rem;
}

/* Header */
.quizHeader {
  width: 100%;
  max-width: 3xl; /* Assuming 3xl is a defined max-width in your context, otherwise use pixel value e.g., 960px */
  margin-bottom: 2rem;
  text-align: center;
}

.quizTitle {
  font-size: 2.25rem; /* 36px */
  line-height: 2.5rem; /* 40px */
  font-weight: 700;
  color: var(--text-color-dark);
  margin-bottom: 0.5rem;
}

.quizSubtitle {
  color: var(--text-color-light);
}

/* Progress Bar */
.progressContainer {
  width: 100%;
  max-width: 3xl;
  margin-bottom: 1.5rem;
}

.progressLabels {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 0.75rem;
}

.progressText {
  font-size: 0.875rem; /* 14px */
  line-height: 1.25rem; /* 20px */
  font-weight: 500;
  color: var(--text-color-light);
}

.progressBar {
  height: 0.5rem; /* 8px */
}

.progressBarTrack {
  border-radius: 9999px; /* Full rounded corners */
  background-color: #e0e7ff; /* Tailwind Blue-100 light background for track */
}

.progressBarIndicator {
  border-radius: 9999px;
  background-image: linear-gradient(
    to right,
    var(--primary-color),
    color-mix(in lch, var(--primary-color) 80%, black)
  ); /* Example gradient - adjust as needed */
}

/* Quiz Card */
.quizCard {
  max-width: 3xl;
  width: 100%;
  background-color: var(--bg-white);
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05); /* Tailwind shadow-xl */
  border-radius: 0.75rem; /* 12px rounded corners */
  border-width: 1px;
  border-color: var(--border-color);
  overflow: hidden;
}

.cardHeader {
  display: flex;
  flex-direction: column;
  @media screen and (min-width: 640px) {
    /* sm breakpoint in Tailwind */
    flex-direction: row;
    align-items: center;
  }
  justify-content: space-between;
  align-items: flex-start;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
  border-bottom-width: 1px;
  border-color: var(--border-color);
  background-color: var(--bg-gray-50);
}

.headerSection {
  display: flex;
  align-items: center;
  gap: 0.75rem;
  margin-bottom: 0.5rem; /* Spacing for stacked layout on smaller screens */
  @media screen and (min-width: 640px) {
    margin-bottom: 0; /* Remove margin for side-by-side layout */
  }
}

.sectionChip {
  font-weight: 600;
}

.questionNumber {
  font-size: 1.125rem; /* 18px */
  line-height: 1.75rem; /* 28px */
  font-weight: 600;
  color: var(--text-color-dark);
  margin-bottom: 0.5rem;
}

.requiredChip {
  font-size: 0.75rem; /* 12px */
}

.timerDisplay {
  display: flex;
  align-items: center;
  gap: 0.5rem;
  color: var(--text-color-light);
}

.timerIcon {
  /* Icon styling if needed */
}

.timerText {
  font-family: monospace; /* Monospace font for timer */
  font-size: 0.875rem; /* 14px */
  line-height: 1.25rem; /* 20px */
}

.timerWarning {
  color: var(--danger-color);
  animation: pulse 1.5s cubic-bezier(0.4, 0, 0.6, 1) infinite; /* Tailwind pulse animation */
}

@keyframes pulse {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.5;
  }
}

.cardBody {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 1.5rem;
  padding-bottom: 1.5rem;
}

.questionText {
  font-size: 1.5rem; /* 24px */
  line-height: 2rem; /* 32px */
  font-weight: 600;
  color: var(--text-color-dark);
  margin-bottom: 1.25rem;
}

.validationError {
  margin-bottom: 1.25rem;
  padding: 1rem;
  background-color: #fef2f2; /* Tailwind Red-50 light background */
  color: var(--danger-color);
  border-radius: 0.5rem; /* 8px rounded corners */
  display: flex;
  align-items: center;
  gap: 0.5rem;
  border-width: 1px;
  border-color: #fecaca; /* Tailwind Red-200 border */
}

/* Input Styles (Textarea, Radio, Slider) */
.textareaBase {
  border-color: var(--border-color);
  transition-property: border-color;
  transition-duration: 200ms;
  &:hover {
    border-color: #9ca3af; /* Tailwind Gray-400 hover border */
  }
}

.textareaInput {
  resize: vertical;
  &:focus {
    border-color: var(--primary-color);
    box-shadow: 0 0 0 2px rgba(59, 130, 246, 0.25); /* Tailwind Blue-200 ring */
    transition-property: border-color, box-shadow;
    transition-duration: 200ms;
    outline: none; /* Remove default focus outline */
  }
}

.radioOption {
  border-width: 1px;
  border-color: var(--border-color);
  border-radius: 0.5rem;
  padding: 0.75rem;
  transition-property: transform, background-color;
  transition-duration: 150ms;
  &:hover {
    background-color: #f9fafb; /* Tailwind Gray-50 hover background */
    transform: scale(1.05);
  }
}

.sliderValueLabels {
  display: flex;
  align-items: center;
  justify-content: space-between;
  margin-bottom: 0.5rem;
}

.sliderMinMaxLabel {
  font-size: 0.875rem; /* 14px */
  line-height: 1.25rem; /* 20px */
  font-weight: 500;
  color: var(--text-color-light);
}

.sliderCurrentValue {
  font-size: 1.25rem; /* 20px */
  line-height: 1.75rem; /* 28px */
  font-weight: 700;
  color: var(--primary-color);
}

.sliderLabels {
  display: flex;
  justify-content: space-between;
  width: 100%;
  margin-top: 0.5rem;
  padding-left: 0.5rem;
  padding-right: 0.5rem;
}

.sliderLabelItem {
  width: calc(100% / 5); /* Assuming 5 labels, adjust if needed */
  text-align: center;
  font-size: 0.75rem; /* 12px */
  color: var(--text-color-light);
}

.sliderTooltipContent {
  background-color: #374151; /* Tailwind Gray-800 tooltip background */
  color: var(--bg-white);
  border-radius: 0.375rem; /* 6px rounded corners */
  padding: 0.25rem 0.5rem;
  font-size: 0.875rem; /* 14px */
}

.hintContainer {
  margin-top: 1.5rem;
}

.hintButton {
  min-width: 2.5rem; /* 40px */
  height: 2.5rem;
  padding: 0;
}

.hintTooltip {
  font-size: 0.875rem; /* 14px */
}

.cardFooter {
  display: flex;
  flex-direction: column;
  @media screen and (min-width: 640px) {
    flex-direction: row;
    align-items: center;
  }
  justify-content: space-between;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 1.25rem;
  padding-bottom: 1.25rem;
  border-top-width: 1px;
  border-color: var(--border-color);
}

.footerInfo {
  display: flex;
  align-items: center;
  color: var(--text-color-light);
}

.infoText {
  display: flex;
  align-items: center;
  gap: 0.25rem;
  font-size: 0.875rem; /* 14px */
}

.footerButtons {
  display: flex;
  gap: 0.75rem;
  margin-top: 0.75rem; /* Spacing for stacked layout on smaller screens */
  @media screen and (min-width: 640px) {
    margin-top: 0; /* Remove margin for side-by-side layout */
  }
}

.prevButton {
  /* Style for previous button if needed */
}

.prevButtonIcon {
  transform: rotate(180deg); /* Rotate icon to point left */
}

.nextButton {
  /* Style for next button if needed */
}

/* Loading State Styles */
.loadingContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: var(--bg-gray-50);
}

.loadingCard {
  text-align: center;
  background-color: var(--bg-white);
  padding: 3rem;
  border-radius: 0.75rem;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

.loadingText {
  margin-top: 1.5rem;
  font-size: 1.25rem; /* 20px */
  font-weight: 600;
  color: var(--text-color-dark);
  animation: pulseText 1.5s cubic-bezier(0.4, 0, 0.6, 1) infinite; /* Pulse animation for text */
}

.loadingSubtitle {
  color: var(--text-color-light);
  margin-top: 0.5rem;
}

@keyframes pulseText {
  0%,
  100% {
    opacity: 1;
  }
  50% {
    opacity: 0.7;
  }
}

/* Error State Styles */
.errorContainer,
.noQuestionsContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  min-height: 100vh;
  background-color: var(--bg-gray-50);
}

.errorCard,
.noQuestionsCard {
  max-width: 48rem; /* 768px */
  width: 100%;
  box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
    0 4px 6px -2px rgba(0, 0, 0, 0.05);
  border-radius: 0.75rem;
}

.errorCard {
  border-width: 2px;
  border-color: var(--danger-color);
}

.errorHeader {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 1rem;
  padding-bottom: 1rem;
  background-color: #fef2f2; /* Tailwind Red-50 error header background */
  color: var(--danger-color);
  border-bottom-width: 1px;
  border-color: #fecaca;
}

.errorIcon {
  color: var(--danger-color);
}

.errorTitle {
  font-size: 1.25rem; /* 20px */
  font-weight: 700;
  margin-top: 0.5rem;
}

.errorBody,
.noQuestionsBody {
  text-align: center;
  padding-top: 2rem;
  padding-bottom: 2rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.errorMessage,
.noQuestionsMessage {
  margin-bottom: 1rem;
  color: var(--text-color-dark);
}

.errorButton,
.noQuestionsButton {
  background-image: linear-gradient(
    to right,
    #dc2626,
    color-mix(in lch, #dc2626 80%, black)
  ); /* Example gradient for error button */
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06); /* Tailwind shadow-md */
  color: var(--bg-white);
  transition-property: box-shadow, transform;
  transition-duration: 200ms;
  &:hover {
    box-shadow: 0 10px 15px -3px rgba(0, 0, 0, 0.1),
      0 4px 6px -2px rgba(0, 0, 0, 0.05); /* Tailwind shadow-lg on hover */
    transform: translateY(-2px); /* Slight lift on hover */
  }
}

/* No Questions State Styles */
.noQuestionsIcon {
  color: var(--warning-color);
}

.noQuestionsTitle {
  font-size: 1.25rem; /* 20px */
  font-weight: 700;
  margin-bottom: 0.5rem;
}

/* Processing Modal Styles */
.processingModal {
  background-color: rgba(
    255,
    255,
    255,
    0.9
  ); /* White with slight transparency for backdrop blur */
  border-radius: 0.75rem;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04); /* Tailwind shadow-2xl */
}

.modalHeader {
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-bottom: 1rem;
  border-bottom-width: 1px;
  border-color: var(--border-color);
  padding-top: 1.5rem;
}

.modalTitle {
  font-size: 1.25rem; /* 20px */
  font-weight: 600;
  color: var(--text-color-dark);
  text-align: center;
}

.modalBody {
  padding-top: 2rem;
  padding-bottom: 2rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  text-align: center;
}

.modalProgress {
  margin-bottom: 1.5rem;
  height: 0.75rem; /* 12px */
  border-radius: 9999px;
}

.modalProgressBarTrack {
  border-radius: 9999px;
  background-color: #e0e7ff; /* Tailwind Blue-100 light background for track */
}

.modalProgressBarIndicator {
  border-radius: 9999px;
  background-image: linear-gradient(
    to right,
    var(--primary-color),
    color-mix(in lch, var(--primary-color) 80%, black)
  );
}

.modalProgressValueLabel {
  font-size: 1.125rem; /* 18px */
  font-weight: 600;
  color: var(--text-color-dark);
}

.modalStatusText {
  color: var(--text-color-light);
  margin-bottom: 0.75rem;
}

.modalSpinner {
  margin-top: 1rem;
}

.analysisCompleteContainer {
  margin-top: 1.5rem;
  padding: 1.25rem;
  background-color: #ecfccb; /* Tailwind Green-100 analysis complete background */
  color: var(--success-color);
  border-radius: 0.5rem;
  border-width: 1px;
  border-color: #bef264; /* Tailwind Green-200 border */
  text-align: center;
}

.analysisCompleteIcon {
  color: var(--success-color);
  margin-bottom: 0.75rem;
}

.analysisCompleteTitle {
  font-weight: 600;
}

.analysisCompleteSubtitle {
  font-size: 0.875rem;
  color: var(--text-color-light);
  margin-top: 0.25rem;
}

.modalFooter {
  padding-top: 1rem;
  border-top-width: 1px;
  border-color: var(--border-color);
  padding-bottom: 1.5rem;
}

.modalResultsButton {
  width: 100%;
  border-radius: 0.375rem;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1),
    0 2px 4px -1px rgba(0, 0, 0, 0.06);
}

/* Review Answers Modal Styles */
.reviewModal {
  background-color: rgba(255, 255, 255, 0.9);
  border-radius: 0.75rem;
  box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1),
    0 10px 10px -5px rgba(0, 0, 0, 0.04);
}

.reviewModalHeader {
  text-align: center;
  padding-top: 1.5rem;
  padding-bottom: 1rem;
}

.reviewModalTitle {
  font-size: 1.25rem; /* 20px */
  font-weight: 600;
  color: var(--text-color-dark);
}

.reviewModalSubtitle {
  color: var(--text-color-light);
  font-size: 0.875rem;
  margin-top: 0.25rem;
}

.reviewModalBody {
  padding-left: 1.5rem;
  padding-right: 1.5rem;
  padding-top: 1rem;
  padding-bottom: 1rem;
}

.reviewQuestionContainer {
  margin-bottom: 1.5rem;
  padding: 1rem;
  border-radius: 0.5rem;
  border-width: 1px;
  border-color: var(--border-color);
  background-color: #f9fafb; /* Tailwind Gray-50 review question background */
}

.reviewQuestionTitle {
  font-size: 1.125rem; /* 18px */
  font-weight: 600;
  color: var(--text-color-dark);
  margin-bottom: 0.75rem;
}

.reviewAnswerText {
  color: var(--text-color-dark);
}

.reviewModalFooter {
  display: flex;
  justify-content: flex-end;
  gap: 0.75rem;
  padding-top: 1rem;
  padding-bottom: 1.5rem;
  padding-left: 1.5rem;
  padding-right: 1.5rem;
}

.reviewModalBackButton,
.reviewModalFinishButton {
  /* Style for review modal buttons if needed */
}
